import React, { useState, useRef, useEffect } from 'react'
import {gsap} from 'gsap'
import { Button } from '../ButtonElement'
import HeroSvg from '../../images/hero.svg'
import { HeroContainer, HeroBg,SeeMore, Line, Para, LeftHero,RightHero,HeroImg, Design, HeroContent, HeroH1, HeroP, HeroBtnWrapper, ArrowForward, ArrowRight } from './HeroElements'

const HeroSection = () => {

    const headerRef = useRef(null);
    const headerRef2 = useRef(null);
    const headerRef1 = useRef(null);
    const seeMoreRef = useRef(null);

    useEffect(() =>{
        gsap.from(headerRef.current, {duration:1, autoAlpha: 0, x: -100, ease: 'back.out(1.7)', delay: 1})
        gsap.from(headerRef2.current, {duration:1, autoAlpha: 0, x: -100, ease: 'back.out(1.7)', delay: 1.5})
        gsap.from(headerRef1.current, {duration:1, autoAlpha: 0, y: 100, ease: 'back.out(1.7)', delay: 1})
        gsap.from(seeMoreRef.current, {duration:1, yoyo:'true', autoAlpha: 0, y: '+=20', repeat:100, ease: 'back.out(4)', delay: 3})
        
        
    }, [])
    const [hover, setHover] = useState(false);

    const onHover = () => {
        setHover(!hover)
    }
    const [hover1, setHover1] = useState(false);

    const onHover1 = () => {
        setHover1(!hover)
    }


    return (
        <HeroContainer>
            <HeroBg>
                {/* <VideoBg autoPlay loop muted src={Video} type='video/mp4'>
                </VideoBg> */}
                {/* <ImgBg src={ImgSvg}>
                </ImgBg> */}
            </HeroBg>
            <HeroContent>
                <LeftHero>
                <HeroH1 ref={headerRef}>
                   Hi, my name is <br /> <Design>Mayur Bahuguna</Design><br /> A Creative Web Developer.
                </HeroH1>
                <Para ref={headerRef2}>Who constantly seeks out innovative solutions everyday business problems. So far, I've honed my analytical thinking and collaboration skills, and I love working with teams.</Para>
                <HeroP>

                </HeroP>
                <HeroBtnWrapper>
                    <Button 
                    to='signup' 
                    onMouseEnter={onHover} 
                    onMouseLeave={onHover}
                    primary= 'true'
                    dark='true'
                    >Github {hover ? <ArrowForward /> : <ArrowRight />}</Button>
                    <Button 
                    to='linkedin' 
                    onMouseEnter={onHover1} 
                    onMouseLeave={onHover1}
                    >Linkedin {hover1 ? <ArrowForward /> : <ArrowRight />}</Button>
                </HeroBtnWrapper>
                <Line> </Line>
                    <SeeMore ref={seeMoreRef}>Scroll Down <ArrowForward /> </SeeMore>
                </LeftHero>
                <RightHero ref={headerRef1}>
                    <HeroImg src={HeroSvg} alt="Hero-SVG" />
                </RightHero>
            </HeroContent>

        </HeroContainer>
    )
}

export default HeroSection
